import { NgModule }             from '@angular/core';
import { RouterModule, Routes } from '@angular/router';


import { home }      from './home/home.component';
import { about }   from './about/about.component';
import { skills }   from './skills/skills.component';
import { eduAndexp }   from './eduAndexp/eduAndexp.component';
//import { porto }   from './porto/porto.component';
import { contact }   from './contact/contact.component';
import {AppComponent } from './app.component'

import { ModuleWithProviders } from '@angular/core';
const routes: Routes = [

  { path: '', redirectTo:'/home' ,pathMatch: 'full'},
  
  {path:'home', component:home},
  { path: 'about', component: about,  pathMatch: 'full' },
  { path: 'skills', component: skills,  pathMatch: 'full' },
  { path: 'eduAndexp', component: eduAndexp,  pathMatch: 'full' },
  //{ path: 'porto', component: porto,  pathMatch: 'full' },
  { path: 'contact', component: contact,  pathMatch: 'full' },

];

@NgModule({
  imports: [ RouterModule.forRoot(routes) ],
  exports: [ RouterModule ]
})
export class AppRoutingModule {}
	


